@tailwind base;
@tailwind components;
@tailwind utilities;

@layer components {
  .option {
    background-color: theme("colors.white");
  }

  .active {
    @apply hover:bg-green-600 bg-green-500 text-primary;
  }

  .inactive {
    @apply hover:bg-green-300 bg-gray-300 text-gray-800;
  }
}